import cornerSquareTypes from "../../constants/cornerSquareTypes";
import { DrawArgs, Window } from "../../types";

export const availableCornerSquareTypes = Object.values(cornerSquareTypes);

export default class QRBorderImage {
  _element?: SVGImageElement;
  _imageUri?: string;
  _window: Window;
  constructor({ imageUri, window }: { imageUri: string; window: Window }) {
    this._imageUri = imageUri;
    this._element = undefined;
    this._window = window;
  }

  draw(x: number, y: number, size: number, rotation: number): void {
    this._drawCustomSvg({ x, y, size, rotation });
  }

  _drawCustomSvg({ x, y, size }: DrawArgs): void {
    const image = this._window.document.createElementNS("http://www.w3.org/2000/svg", "image");
    image.setAttribute("href", this._imageUri || "");
    image.setAttribute("xlink:href", this._imageUri || "");
    image.setAttribute("x", String(x));
    image.setAttribute("y", String(y));
    image.setAttribute("width", `${size}px`);
    image.setAttribute("height", `${size}px`);

    this._element = image;
  }
}
